<svelte:options tag="fds-progress"></svelte:options>
<script>
    import { seriesStore} from "./stores"
    import RadialProgressBar from './radialProgressBar.svelte';
    import fdsHelper from "@fds-components/fds-helper"
    import { beforeUpdate,onMount} from 'svelte';
    import { get_current_component } from "svelte/internal";
    const path = fdsHelper.get_href()
    export let series = []
    export let style = "default" // [radial , semicircle]
    export let thickness = style == "radial" || style == "semicircle" ? 5 : null
    export let stackSeries = true
    export let margin = 0
    export let valueLabel = null
    export let width = "100%"
    export let duration;

    export let colors = ["#FFC107", "#4CAF50", "#03A9F4"]

    //Array of classes / colors that must be applied to the stops whenever the progress percent exceeds the threshold
    export let thresholds = []

    if (width == "auto") width = "100%"
    let host = get_current_component();
    let actWidth = 0
    let stylepath;
    let styleappended;
    let store = null;

    onMount(() => {
        setTimeout(() => {
            store = seriesStore(series, {
                valueLabel,
                colors,
                thresholds,
                stackSeries,
                thickness,
                margin,
                duration
            })
        }, "100");
    })



    $: if(path) {
        stylepath="<style>\n" +
            "    @import url("+path+"style.css);\n" +
            "</style>";
    }

    $: if(store) console.log("store",$store);

    $: if(store)  {store.updateSeries(series)}
    $: if(store) {
        if (valueLabel != null) store.updateLabel(valueLabel)
    }

    beforeUpdate(() => {

        if (!styleappended && stylepath){
            styleappended = true;
            var divNode = document.createElement("div");
            divNode.innerHTML = stylepath ;
            host.shadowRoot.appendChild(divNode);
        }
    })

    export function updatePerc(perc, seriesIdx = 0) {
        store.updatePerc(perc, seriesIdx)
    }

    export function updateSeries(_series) {
        series = _series
    }

</script>
{#if store}
    <div bind:clientWidth={actWidth} class="pb_cnt" style="width:{typeof width == 'string' ? width : width + 'px'}">
            <RadialProgressBar
                    {...$$props}
                    {store}
                    {colors}
                    {thresholds}
                    {actWidth}>
                <slot></slot>
            </RadialProgressBar>
    </div>
{/if}