<script>
    import Arc from './arc.svelte';
    import SeriesArc from './seriesArc.svelte';
    import ProgressLabel from './progressLabel.svelte';
    import {seriesStore} from './stores';

    export let thickness = 5;
    export let textSize = null;
    export let showProgressValue = true;
    export let stackSeries  = false;
    export let margin  = 0;
    export let addBackground  = true;
    export let bgColor  = '#e5e5e5';
    export let bgFillColor  = 'transparent';
    export let labelColor  = '#555';
    export let invLabelColor  = '#fff';
    export let startAngle  = 0;
    export let endAngle  = 360;
    export let colors;
    export let thresholds ;
    export let store ;
    export let style ;
    export let cls = '';
    export let labelAlignX;
    export let labelAlignY;
    export let actWidth = 0;

    let vbHeight = 100;
    let actHeight = 0;
    let scaleX = 1;
    let scaleY = 1;

    $: {
        if(style == 'semicircle') {
            actHeight = actWidth / 2;
            vbHeight = 50;
        }
        else {
            actHeight = actWidth;
            vbHeight = 100;
        }

        if(actWidth > 0)
            scaleX = 100 / actWidth;
        if(actHeight > 0)
            scaleY = vbHeight / actHeight;
    }

    if(style == 'semicircle') {
        if(!labelAlignY)
            labelAlignY = 'bottom';

        startAngle = -90;
        endAngle = 90;
    }
    else {
        if(!labelAlignY)
            labelAlignY = 'middle';
    }

    const ts = new Date().getTime();
    const maskId = 'tx_mask_' + ts + Math.floor(Math.random() * 999);

    if(textSize == null)
        textSize = 100;

    const maskSeries = [{
        perc: $store.overallPerc,
        radius: 50 - (thickness * $store.series.length),
        color: '#fff'
    }];

    const maskStore = seriesStore(maskSeries, {colors, thresholds, stackSeries: false, thickness, margin});

</script>

<svg className="progressbar progressbar-{style} {cls}" viewBox="0 0 100 {vbHeight}" width="100%" height="{actHeight}"
     xmlns="http://www.w3.org/2000/svg">

    {#if showProgressValue}
        <defs>
            <mask id="{maskId}" x="0" y="0" width="100" height="100%">
                <SeriesArc store={maskStore} serieIdx={0} {thickness} {startAngle} {endAngle}/>
                <Arc radius={50 - thickness} fill="#fff" {startAngle} {endAngle} closeArc={true}/>
            </mask>
        </defs>
    {/if}

    <!-- If series don't have to be stacked, add only one background arc -->
    {#if addBackground && stackSeries}
        <Arc radius={$store.series[0].radius} fill="{bgFillColor}" {startAngle} {endAngle} strokeWidth={thickness}
             stroke={bgColor}/>
    {/if}

    {#each $store.series as serie, idx}
        <!-- If series have to be stacked, add one background arc with concentric radius for each series  -->
        {#if !stackSeries && addBackground}
            <Arc radius={serie.radius} fill="{bgFillColor}" {startAngle} {endAngle} strokeWidth={thickness}
                 stroke={bgColor}/>
        {/if}
        <SeriesArc {store} serieIdx={idx} {thickness} {startAngle} {endAngle} {stackSeries}/>
    {/each}

    {#if showProgressValue}
        <ProgressLabel
                {store}
                {textSize}
                {labelColor}
                {invLabelColor}
                {maskId}
                {style}
                {labelAlignX}
                {labelAlignY}
                {scaleX}
                {scaleY}>
        </ProgressLabel>
    {/if}
    <slot></slot>
</svg>